import React from "react";
import { useState } from "react";

import NavigationB from "./components_browser/NavigationB";
import StartB from "./components_browser/StartB";
import AboutMeB from "./components_browser/AboutMeB";
import ProjectsB from "./components_browser/ProjectsB";
import QualificationB from "./components_browser/QualificationB";
import SkillsB from "./components_browser/SkillsB";
import ContactB from "./components_browser/ContactB";

import NavigationM from "./components_mobile/NavigationM";
import StartM from "./components_mobile/StartM";
import AboutMeM from "./components_mobile/AboutMeM";
import ProjectsM from "./components_mobile/ProjectsM";
import QualificationM from "./components_mobile/QualificationM";
import SkillsM from "./components_mobile/SkillsM";
import ContactM from "./components_mobile/ContactM";

import cvdata from "./data/cvdata.json";
import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile,
} from "react-device-detect";

const App = () => {
  const [navigation, setNavigation] = useState([
    {
      id: 1,
      name_pl: "Start",
      name_eng: "Start",
      current: true,
    },
    {
      id: 2,
      name_pl: "O Mnie",
      name_eng: "About Me",
      current: false,
    },
    {
      id: 3,
      name_pl: "Umiejtnoci",
      name_eng: "Skills",
      current: false,
    },
    {
      id: 4,
      name_pl: "Kwalifikacje",
      name_eng: "Qualification",
      current: false,
    },
    {
      id: 5,
      name_pl: "Projekty",
      name_eng: "Projects",
      current: false,
    },
    {
      id: 6,
      name_pl: "Kontakt",
      name_eng: "Contact",
      current: false,
    },
  ]);
  const [language, setLanguage] = useState("eng");

  const changeCurrent = (current) => {
    const changedNavigation = [...navigation];
    changedNavigation.forEach((element) => {
      if (element.id === current) {
        element.current = true;
      } else {
        element.current = false;
      }
    });
    setNavigation(changedNavigation);
    window.scrollTo(0, 0);
  };

  const projectC = () => {
    const current = navigation.find((element) => element.current === true);

    switch (current.name_eng) {
      case "Start":
        return <StartB cvdata={cvdata} />;

      case "About Me":
        return <AboutMeB cvdata={cvdata} />;

      case "Projects":
        return <ProjectsB cvdata={cvdata} />;

      case "Qualification":
        return <QualificationB cvdata={cvdata} />;

      case "Skills":
        return <SkillsB cvdata={cvdata} />;

      case "Contact":
        return <ContactB cvdata={cvdata} />;

      default:
        return <StartB cvdata={cvdata} />;
    }
  };

  const projectM = () => {
    const current = navigation.find((element) => element.current === true);

    switch (current.name_eng) {
      case "Start":
        return <StartM cvdata={cvdata} />;

      case "About Me":
        return <AboutMeM cvdata={cvdata} />;

      case "Projects":
        return <ProjectsM cvdata={cvdata} />;

      case "Qualification":
        return <QualificationM cvdata={cvdata} />;

      case "Skills":
        return <SkillsM cvdata={cvdata} />;

      case "Contact":
        return <ContactM cvdata={cvdata} />;

      default:
        return <StartM cvdata={cvdata} />;
    }
  };

  return (
    <div>
      <BrowserView>
        <NavigationB
          navigation={navigation}
          setNavigation={setNavigation}
          changeCurrent={changeCurrent}
        />
        <div className="bg-gray-50 mt-[64px]">{projectC()}</div>
      </BrowserView>
      <MobileView>
        <NavigationM
          navigation={navigation}
          setNavigation={setNavigation}
          changeCurrent={changeCurrent}
        />
        <div className="bg-gray-50 mt-[64px]">{projectM()}</div>
      </MobileView>
    </div>
  );
};

export default App;
